.loading__container
  position: absolute
  top: 50%
  left: 50%
  .fire
    position: absolute
    top: calc(50% - 25px)
    left: calc(50% - 25px)
    width: 50px
    height: 50px
    background-color: transparent
    margin-left: auto
    margin-right: auto
    .fire-center
      height: 100%
      width: 100%
      position: absolute
      animation: shake 2s ease-out 0
      animation-iteration-count: infinite
      animation-fill-mode: both
      .main-fire
        position: absolute
        top: 5%
        right: -5%
        width: 100%
        height: 100%
        background-color: #3cff1e
        transform: scaleX(0.8) rotate(45deg)
        border-radius: 0 40% 60% 40%
        filter: drop-shadow(0 0 10px #3cff1e)
      .particle-fire
        position: absolute
        top: 15%
        left: 40%
        width: 15px
        height: 15px
        background-color: #3cff1e
        transform: scaleX(0.8) rotate(45deg)
        border-radius: 50%
        filter: drop-shadow(0 0 10px #3cff1e)
        animation: particleUp 2s ease-out 0
        animation-iteration-count: infinite
        animation-fill-mode: both
    .fire-right
      height: 100%
      width: 100%
      position: absolute
      animation: shake 2s ease-out 0
      animation-iteration-count: infinite
      animation-fill-mode: both
      .main-fire
        position: absolute
        top: 15%
        right: -25%
        width: 80%
        height: 80%
        background-color: #3cff1e
        transform: scaleX(0.8) rotate(45deg)
        border-radius: 0 40% 60% 40%
        filter: drop-shadow(0 0 10px #3cff1e)
      .particle-fire
        position: absolute
        top: 45%
        left: 50%
        width: 15px
        height: 15px
        background-color: #3cff1e
        transform: scaleX(0.8) rotate(45deg)
        border-radius: 50%
        filter: drop-shadow(0 0 10px #3cff1e)
        animation: particleUp 2s ease-out 0
        animation-iteration-count: infinite
        animation-fill-mode: both
    .fire-left
      position: absolute
      height: 100%
      width: 100%
      animation: shake 3s ease-out 0
      animation-iteration-count: infinite
      animation-fill-mode: both
      .main-fire
        position: absolute
        top: 15%
        left: -20%
        width: 80%
        height: 80%
        background-color: #3cff1e
        transform: scaleX(0.8) rotate(45deg)
        border-radius: 0 40% 60% 40%
        filter: drop-shadow(0 0 10px #3cff1e)
      .particle-fire
        position: absolute
        top: 10%
        left: 20%
        width: 10%
        height: 10%
        background-color: #3cff1e
        border-radius: 50%
        filter: drop-shadow(0 0 10px #d43322)
        animation: particleUp 3s infinite ease-out 0
        animation-fill-mode: both
    .fire-bottom
      .main-fire
        position: absolute
        top: 30%
        left: 20%
        width: 75%
        height: 75%
        background-color: #3cff1e
        transform: scaleX(0.8) rotate(45deg)
        border-radius: 0 40% 100% 40%
        filter: blur(10px)
        animation: glow 2s ease-out 0
        animation-iteration-count: infinite
        animation-fill-mode: both

@keyframes scaleUpDown
  0%,
  100%
    transform: scaleY(1) scaleX(1)
    transform: scaleY(1.1)
  75%
    transform: scaleY(0.95)
  80%
    transform: scaleX(0.95)

@keyframes shake
  0%,
  100%
    transform: skewX(0) scale(1)
  50%
    transform: skewX(5deg) scale(0.9)

@keyframes particleUp
  0%
    opacity: 0
  20%
    opacity: 1
  80%
    opacity: 1
  100%
    opacity: 0
    top: -100%
    transform: scale(0.5)

@keyframes glow
  0%,
  100%
    background-color: #3cff1e
  50%
    background-color: #3cff1e
