.TodoCompleted-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.TodoCompleted-container h2 {
  font-size: 20px;
  margin-bottom: 20px;
}
.TodoCompleted-container .graphic-container {
  position: relative;
}
.TodoCompleted-container .graphic-container p {
  position: absolute;
  top: 42%;
  left: 36%;
  font-size: 35px;
  margin: 0;
  padding: 0;
  line-height: 1;
  font-weight: 900;
  color: #85e486;
}

